<template>
  <div id="app" style=""><br>
    <div>
     <el-row :gutter="10">
        <el-col :xs="4" :sm="4" :md="7" :lg="7" :xl="7">&nbsp{{radio1}}{{radio2}}{{radio3}}{{radio4}}{{radio5}}</el-col>
        <el-col :xs="2" :sm="2" :md="6" :lg="6" :xl="6">&nbsp</el-col>
        <el-col :xs="12" :sm="12" :md="6" :lg="6" :xl="6">
            <div>
              <el-input
                placeholder=""
                v-model="input"
                clearable
                suffix-icon="el-icon-search">   
                 </el-input>       
            </div>
        </el-col>
     <el-col :xs="5" :sm="5" :md="3" :lg="3" :xl="3"><el-button></el-button></el-col>
     <el-col :xs="1" :sm="1" :md="2" :lg="2" :xl="2">&nbsp</el-col>
   </el-row>
    <br><br><br>

<el-button type="text" @click="dialogFormVisible = true"></el-button>
 <el-dialog title="" :visible.sync="dialogFormVisible">
 <el-row>
  <el-col :span="5"><p></p></el-col>
  <el-col :span="19"><div>
       <el-radio-group v-model="radio1">
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label="APP"></el-radio-button>
      <el-radio-button label=""></el-radio-button>
       <el-radio-button label="JAVA"></el-radio-button>
      <el-radio-button label="C"></el-radio-button>
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
    </el-radio-group>
      </div></el-col>
</el-row>
<el-row>
  <el-col :span="5"><p></p></el-col>
  <el-col :span="19"><div>
       <el-radio-group v-model="radio2">
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
    </el-radio-group>
      </div></el-col>
</el-row>
<el-row>
  <el-col :span="5"><p></p></el-col>
  <el-col :span="19"><div>
       <el-radio-group v-model="radio3">
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label="5">5</el-radio-button>
      <el-radio-button label="15">5~15</el-radio-button>
      <el-radio-button label="16">15</el-radio-button>
    </el-radio-group>
      </div></el-col>
</el-row>
<el-row>
  <el-col :span="5"><p></p></el-col>
  <el-col :span="19"><div>
       <el-radio-group v-model="radio4">
      <el-radio-button label="1">1</el-radio-button>
      <el-radio-button label="2">2</el-radio-button>
      <el-radio-button label="3">3</el-radio-button>
      <el-radio-button label="4">4</el-radio-button>
      <el-radio-button label="5">5</el-radio-button>
    </el-radio-group>
      </div></el-col>
</el-row>
<el-row>
  <el-col :span="5"><p></p></el-col>
  <el-col :span="19"><div>
       <el-radio-group v-model="radio5">
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
    </el-radio-group>
      </div></el-col>
</el-row>
 <el-row>
  <el-col :span="12"><p></p></el-col>
  <el-col :span="12">
      <div>
          <el-button type="primary"  @click="doFilter"></el-button>
      </div>
      </el-col>
</el-row>
</el-dialog>

   <br>
   </div>
   <div>
  <el-row>
    <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="item in tableDataEnd" :key="o" :offset="0" >        
      <el-card  shadow="hover">
        <el-row>
          <el-col :span="12">
        <img src="../assets/logo.png" class="image">
          </el-col>
           <el-col :span="12">
        <div style="padding: 14px;">
            <span>id{{item.id}}</span><br>
            <span>{{item.type}}</span><br>
            <span>{{item.address}}</span>
            <div class="bottom clearfix">
            <el-button type="text" class="button"></el-button>
            </div>       
        </div>   
        </el-col>
        </el-row>
        </el-card>
    </el-col>
    </el-row>
     <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalItems">
 </el-pagination>

   </div>
  </div>
</template>

<script>
export default {
 data() {
 return {
  dialogFormVisible: false,
  radio1:'',
  radio2:'',
  radio3:'',
  radio4:'',
  radio5:'',
  tableDataBegin: [
  {
   id:1,
   name: "",
   address: "",
   type:"C"

  },
  {
   id:2,
   name: "",
   address: "",
   type:"C"
  },
  {
      id:3,
   date: "2016-05-03",
   name: "",
   address: "",
   type:"C"
  },
  {
      id:4,
   date: "2016-05-04",
   name: "",
   address: "",
   type:"C"
  },
  {
     id:5,
   date: "2016-05-05",
   name: "",
   address: "",
   type:"JAVA"
  },
  {
      id:6,
   date: "2016-05-06",
   name: "",
   address: "",
   type:"JAVA"
  },
  {
      id:7,
   date: "2016-05-07",
   name: "",
   address: "",
   type:"JAVA"
  },
  {
      id:8,
   date: "2016-05-08",
   name: "",
   address: "",
   type:""
  },
  {
      id:9,
   date: "2016-05-01",
   name: "",
   address: "",
   type:""
  },
  {
     id:10,
   date: "2016-05-02",
   name: "",
   address: "",
   type:""
  },
  ],
  tableDataName: "",
  tableDataEnd: [],
  currentPage: 4,
  pageSize: 2,
  totalItems: 0,
  filterTableDataEnd:[],
  flag:false
 };
 },
 created() {
 this.totalItems = this.tableDataBegin.length;
 if (this.totalItems > this.pageSize) {
  for (let index = 0; index < this.pageSize; index++) {
  this.tableDataEnd.push(this.tableDataBegin[index]);
  }
  // this.tableDataEnd = this.tableDataBegin;
 } else {
  this.tableDataEnd = this.tableDataBegin;
 }
 },
 methods: {
 //,
 //currentChangePage
 doFilter() {
//   if (this.tableDataName == "") {
//   this.$message.warning("");
//   return;
//   }
  this.dialogFormVisible = false;
  this.tableDataEnd = []
  //,
  this.filterTableDataEnd=[]
  this.tableDataBegin.forEach((value, index) => {
  if(value.type){
   if((value.type.indexOf(this.radio1)>=0)&&(value.address.indexOf(this.radio5)>=0)){
   this.filterTableDataEnd.push(value)
   }
   
  }
//   if(value.address){
//    if(value.address.indexOf(this.radio5)>=0){
//    this.filterTableDataEnd.push(value)
//    }
//   }
  });
  //
  this.currentPage=1
  this.totalItems=this.filterTableDataEnd.length
  //,
  this.currentChangePage(this.filterTableDataEnd)
  //
  this.flag=true
 },
 openData() {},
 handleSizeChange(val) {
  console.log(` ${val} `);
  this.pageSize = val;
  this.handleCurrentChange(this.currentPage);
 },
 handleCurrentChange(val) {
  console.log(`: ${val}`);
  this.currentPage = val;
  //
  if(!this.flag){
  this.currentChangePage(this.tableDataEnd)
  }else{
  this.currentChangePage(this.filterTableDataEnd)  
  }
 }, //
 currentChangePage(list) {
  let from = (this.currentPage - 1) * this.pageSize;
  let to = this.currentPage * this.pageSize;
  this.tableDataEnd = [];
  for (; from < to; from++) {
  if (list[from]) {
   this.tableDataEnd.push(list[from]);
  }
  }
 }
 }
};
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
}
</style>



