        submitUpload() {
        this.$refs.upload.submit();
      },
      choosejia(){
        this.jia=false;
        this.yi=true;
        this.seen3=true;
        this.show=true;
      },
       chooseyi(){
        this.jia=true;
        this.yi=false;
        this.show=true;
        this.seen3=true;
      },
        
    },
    
   mounted() {   

    if(this.$route.query.type==="")
    {
      this.seen1=true;
      this.seen2=false;
    }else
    {
      this.seen1=false;
      this.seen2=true;
    }



    axios.get('http://localhost:8081/readers', {      
    executingproject_id:this.$route.query.executingproject_id
      
    })
    .then(function (response) {
      console.log(response);
      this.info=response;
    })
    .catch(function (error) {
      console.log(error);
    }); 
       
   },
}
</script>
<style>
/* */
#hello{
  font-size: 25px;
  color:slategray;
  /* color: #297fca; */
  text-align: left;
}
.f1{
   
    font-size:2rem;
    margin-left:5rem;
    margin-right:5rem;
    margin-top:2rem;
    text-align: center;
    background-color: white;
    border-radius:25px 25px 10px 10px;

}
.container{
    font-size:1.4rem;
    }
.image {
    max-width: 100%;
    height: auto;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to 
 {
  opacity: 0;
}

  @media(max-width: 960px) {
    .f1 {
      width: 100%;
      margin: -1rem  0 0.5rem 0;
      font-size:15px;
      text-align: center;
    }
    .image {
        max-width: 80%;
        height: auto;
    }
    .container{
        font-size: 15px;
    }
    }
</style>



