import axios from 'axios'
import thefooter from '../components/footer2.vue'

export default {
    name:'studio-register',
    data() {
      return {
         fileList: [],
        options2: [{
          label: '',
          cities: []
        }, {
          label: '',
          cities: []
        }],
        props: {
          value: 'label',
          children: 'cities'
        },
        StudioRegisterform: {
          studio_username: '',
          studio_code:'',
          studio_code_confirmed:'',
          tel:'',
          textVerification:'',
          region: '',
          studio_name:'',
          studio_tel:'',
          studio_email:'',
          studio_license:'',
          studio_taxFileNum:'',
          studio_account:'',
          studio_address:'',
          studio_info:'',
          checkcode:'',
          date1: '',
          date2: '',
          delivery: false,
          isagree: "",
          resource: '',
          desc: ''
        },
        seen1: true,
        seen2:false,
        input:'',
        textarea: '',
      };
    },
    components:{
      'thefooter':thefooter,
    },
    methods:{
        submitUpload() {
        this.$refs.upload.submit();
      },
        StudioRegister(){

        if(this.StudioRegisterform.isagree==true)
        {
            this.$refs.upload.submit();
            // axios.post('http://localhost:8080/readers/',{           
            //     company_username:this.CompanyRegisterform.company_username,
            //     company_password:this.CompanyRegisterform.company_code
            // })
            //  this.$refs.upload.submit();
                  
            // setTimeout(function (){
            //   alert("ddd");
            // },"3000");
            this.$router.push({
                path:'/',
                query:{

                }
            });

        }else{
          alert("");
        }
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
        enterNextRegisterPage(){
          this.seen1=false;
          this.seen2=true;
      },
      enterPreviousRegisterPage(){
          this.seen1=true;
          this.seen2=false;
      },
       handleItemChange(val) {
        console.log('active item:', val);
        setTimeout(_ => {
          if (val.indexOf('') > -1 && !this.options2[0].cities.length) {
            this.options2[0].cities = [{
              label: ''
            }];
          } else if (val.indexOf('') > -1 && !this.options2[1].cities.length) {
            this.options2[1].cities = [{
              label: ''
            }];
          }
        }, 300);
      },
      
  }
}
</script>
