            executingproject_id:this.$route.query.executingproject_id,
            executingproject_name:this.$route.query.executingproject_name,
            executingproject_state:this.$route.query.executingproject_state,
            isapplyed:false,
            seen1:false,
            seen2:false,
            seen3:false,
            seen4:false,
            seen5:false,
            seen6:false,
            seen7:false,
            seen8:false,
            seen9:false,
            img1:require('../../assets/logo.png'),
            info:null,
            percentage:"40"
        }
    },
    methods: {
        submitUpload() {
        this.$refs.upload.submit();
      },
      applyrprogresspayment(){

        // axios.get('http://localhost:8081/readers', {      
        // executingproject_id:this.$route.query.executingproject_id
        
        // })
        // .then(function (response) {
        // console.log(response);
        // this.info=response;
        // })
        // .catch(function (error) {
        // console.log(error);
        // }); 
          this.$notify({
          title: '',
          message: '',
          type: 'success'
        });
        this.executingproject_state="",
        this.isapplyed=true;
      },
      arbitration(){
          this.$router.push({
              path:'/studioafterlogin/arbitration',
              query:{
                  studio_id:this.$route.query.studio_id,
                  executingproject_id:this.executingproject_id
              }
          })
      }
        
    },
    
   mounted() {  
       if(this.$route.query.executingproject_state=="")
       {
           this.seen1=true;
           this.seen2=false;
           this.seen3=false;
       }else if(this.$route.query.executingproject_state=="")
       {
           this.seen1=false;
           this.seen2=true;
           this.seen3=false;
       }else if(this.$route.query.executingproject_state=="")
       {
           this.seen1=false;
           this.seen2=false;
           this.seen3=true;
       }else if(this.$route.query.executingproject_state=="")
       {
           this.seen4=true;
       }
       else if(this.$route.query.executingproject_state=="")
       {
           this.seen5=true;
       }
       else if(this.$route.query.executingproject_state=="")
       {
            this.seen6=true;
       }else if(this.$route.query.executingproject_state=="")
       {
            this.seen7=true;
       }else if(this.$route.query.executingproject_state=="")
       {
           this.seen8=true;
       }else if(this.$route.query.executingproject_state=="")
       {
           this.seen9=true;
       }
        axios.get('http://localhost:8081/readers', {      
        executingproject_id:this.$route.query.executingproject_id
        
        })
        .then(function (response) {
        console.log(response);
        this.info=response;
        })
        .catch(function (error) {
        console.log(error);
        }); 
       
   },
}
</script>
