<template>
<div>
 <el-input v-model="tableDataName" placeholder="" style="width:240px"></el-input>
 <el-button type="primary" @click="doFilter"></el-button>
 <el-button type="primary" @click="openData"></el-button>
 <el-table
 :data="tableDataEnd"
 border
 style="width: 100%">
 <el-table-column
  prop="date"
  label=""
  width="180">
 </el-table-column>
 <el-table-column
  prop="name"
  label=""
  width="180">
 </el-table-column>
 <el-table-column
  prop="address"
  label="">
 </el-table-column>
 </el-table>
 <!-- <el-row :gutter="20">
    <el-col :xs="20" :sm="10" :md="10" :lg="6" :xl="6" v-for="item in tableDataEnd" :key="o" :offset="1" > 
        <el-card  shadow="hover">
        <img src="../assets/logo.png" class="image">
        <div style="padding: 14px;">
            <span>{{item.date}}</span>
            <div class="bottom clearfix">
            <el-button type="text" class="button"></el-button>
            </div>
        </div>
        </el-card>
    </el-col>
    </el-row> -->


 <el-pagination
  @size-change="handleSizeChange"
  @current-change="handleCurrentChange"
  :current-page="currentPage"
  :page-sizes="[1, 2, 3, 4]"
  :page-size="pageSize"
  layout="total, sizes, prev, pager, next, jumper"
  :total="totalItems">
 </el-pagination>
</div>
</template>
<script>
export default {
 data() {
 return {
  tableDataBegin: [
  {
   date: "2016-05-01",
   name: "",
   address: " 1518 "
  },
  {
   date: "2016-05-02",
   name: "",
   address: " 1517 "
  },
  {
   date: "2016-05-03",
   name: "",
   address: " 1519 "
  },
  {
   date: "2016-05-04",
   name: "",
   address: " 1516 "
  },
  {
   date: "2016-05-05",
   name: "",
   address: " 1518 "
  },
  {
   date: "2016-05-06",
   name: "",
   address: " 1517 "
  },
  {
   date: "2016-05-07",
   name: "",
   address: " 1519 "
  },
  {
   date: "2016-05-08",
   name: "",
   address: " 1516 "
  }
  ],
  tableDataName: "",
  tableDataEnd: [],
  currentPage: 4,
  pageSize: 2,
  totalItems: 0,
  filterTableDataEnd:[],
  flag:false
 };
 },
 created() {
 this.totalItems = this.tableDataBegin.length;
 if (this.totalItems > this.pageSize) {
  for (let index = 0; index < this.pageSize; index++) {
  this.tableDataEnd.push(this.tableDataBegin[index]);
  }
 } else {
  this.tableDataEnd = this.tableDataBegin;
 }
 },
 methods: {
 //,
 //currentChangePage
 doFilter() {
  if (this.tableDataName == "") {
  this.$message.warning("");
  return;
  }
  this.tableDataEnd = []
  //,
  this.filterTableDataEnd=[]
  this.tableDataBegin.forEach((value, index) => {
  if(value.name){
   if(value.name.indexOf(this.tableDataName)>=0){
   this.filterTableDataEnd.push(value)
   }
  }
  });
  //
  this.currentPage=1
  this.totalItems=this.filterTableDataEnd.length
  //,
  this.currentChangePage(this.filterTableDataEnd)
  //
  this.flag=true
 },
 openData() {},
 handleSizeChange(val) {
  console.log(` ${val} `);
  this.pageSize = val;
  this.handleCurrentChange(this.currentPage);
 },
 handleCurrentChange(val) {
  console.log(`: ${val}`);
  this.currentPage = val;
  //
  if(!this.flag){
  this.currentChangePage(this.tableDataEnd)
  }else{
  this.currentChangePage(this.filterTableDataEnd)  
  }
 }, //
 currentChangePage(list) {
  let from = (this.currentPage - 1) * this.pageSize;
  let to = this.currentPage * this.pageSize;
  this.tableDataEnd = [];
  for (; from < to; from++) {
  if (list[from]) {
   this.tableDataEnd.push(list[from]);
  }
  }
 }
 }
};
</script>


